'use client';

import { useMemo } from 'react';
import Image from 'next/image';
import Link from 'next/link';
import EmptyState from '@/components/EmptyState';
import { useSettings, useProcessEntries, useArtworks } from '@/hooks';
import { Play, Clock, Palette, Layers, Sparkles, HelpCircle } from 'lucide-react';
import { getImageUrl } from '@/lib/utils';

export default function Process() {
  const { settings, loading: settingsLoading } = useSettings();
  const { entries, loading: processLoading } = useProcessEntries({ visibility: 'published' });
  const { artworks, loading: artworksLoading } = useArtworks();

  const combinedProcesses = useMemo(() => {
    return entries.map(p => ({
      ...p,
      artwork: artworks.find(a => a.id === p.artworkId),
    }));
  }, [entries, artworks]);

  const loading = settingsLoading || processLoading || artworksLoading;

  if (loading) {
    return (
      <div className="min-h-screen bg-[var(--bg-primary)] flex items-center justify-center pt-20">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-[var(--accent-primary)]"></div>
      </div>
    );
  }

  const pageTitle = settings?.processPageTitle || 'My Process';
  const pageSubtitle = settings?.processPageSubtitle || 'Behind-the-scenes look at how ideas transform into finished artwork';

  const iconMap: Record<string, any> = {
    Palette, Layers, Sparkles, Play, Clock, HelpCircle
  };

  return (
    <div className="min-h-screen bg-[var(--bg-primary)] text-[var(--text-primary)] pt-20 pb-16">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header */}
        <div className="text-center mb-16 animate-fade-in-up">
          <h1 className="text-4xl md:text-5xl font-bold mb-4 text-[var(--text-primary)] font-dancing">
            {pageTitle.split(' ').map((word, i, arr) =>
              i === arr.length - 1 ? <span key={i} className="text-gradient"> {word}</span> : (i === 0 ? word : ` ${word}`)
            )}
          </h1>
          <p className="text-xl text-[var(--text-secondary)] max-w-2xl mx-auto">
            {pageSubtitle}
          </p>
        </div>

        {/* Process Steps Overview */}
        {settings?.processOverviewSteps && settings.processOverviewSteps.length > 0 && (
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-16">
            {settings.processOverviewSteps.map((step, index) => {
              const IconComp = iconMap[step.icon] || Palette;
              return (
                <div
                  key={index}
                  className="card p-4 text-center group animate-fade-in-up"
                  style={{ animationDelay: `${index * 100}ms` }}
                >
                  <div className="w-12 h-12 mx-auto mb-3 rounded-xl bg-gradient-to-br from-[var(--accent-primary)] to-[var(--accent-secondary)] flex items-center justify-center text-white group-hover:scale-110 transition-transform duration-300">
                    <IconComp className="w-6 h-6" />
                  </div>
                  <h3 className="font-semibold text-[var(--text-primary)]">{step.label}</h3>
                  <p className="text-xs text-[var(--text-secondary)]">{step.desc}</p>
                </div>
              );
            })}
          </div>
        )}

        {/* Process Cards or Empty State */}
        {combinedProcesses.length === 0 ? (
          <EmptyState
            type="process"
            actionLabel="View Portfolio"
            actionHref="/portfolio"
          />
        ) : (
          <div className="space-y-16">
            {combinedProcesses.map((process, processIndex) => (
              <article
                key={process.id}
                className="animate-fade-in-up"
                style={{ animationDelay: `${400 + processIndex * 100}ms` }}
              >
                <div className="card overflow-hidden">
                  <div className="grid grid-cols-1 lg:grid-cols-2 gap-0">
                    {/* Image/Video Section */}
                    <div className={`relative ${processIndex % 2 === 1 ? 'lg:order-2' : ''}`}>
                      <div className="relative aspect-video lg:aspect-auto lg:h-full min-h-[300px]">
                        <Image
                          src={getImageUrl(process.artwork?.cdnUrl, process.artwork?.imageUrl, process.artwork?.id || process.id)}
                          alt={process.artwork?.title || 'Process'}
                          fill
                          className="object-cover"
                        />
                        <div className="absolute inset-0 bg-gradient-to-t from-black/60 via-transparent to-transparent" />

                        {/* Play Button Overlay */}
                        {process.youtubeShortUrl && (
                          <div className="absolute inset-0 flex items-center justify-center">
                            <button className="group/play p-6 bg-white/20 backdrop-blur-md rounded-full hover:bg-white/30 transition-all duration-300 hover:scale-110">
                              <Play className="w-10 h-10 text-white fill-white group-hover/play:scale-110 transition-transform" />
                            </button>
                          </div>
                        )}
                      </div>
                    </div>

                    {/* Content Section */}
                    <div className="p-6 lg:p-8 flex flex-col justify-center">
                      <h2 className="text-2xl font-bold mb-4 text-[var(--text-primary)]">
                        {process.artwork?.title || 'Process Breakdown'}
                      </h2>
                      <p className="text-[var(--text-secondary)] mb-6 leading-relaxed">
                        {process.processNotes}
                      </p>

                      {/* Steps */}
                      {process.stepLabels && process.stepLabels.length > 0 && (
                        <div className="grid grid-cols-2 gap-3 mb-6">
                          {process.stepLabels.map((step, stepIndex) => (
                            <div
                              key={stepIndex}
                              className="p-3 rounded-lg bg-[var(--bg-tertiary)] hover:bg-[var(--accent-primary)]/10 transition-colors duration-300"
                            >
                              <div className="flex items-center gap-2">
                                <span className="w-6 h-6 rounded-full bg-[var(--accent-primary)] text-white text-xs flex items-center justify-center font-bold">
                                  {stepIndex + 1}
                                </span>
                                <span className="font-medium text-sm text-[var(--text-primary)]">{step}</span>
                              </div>
                            </div>
                          ))}
                        </div>
                      )}

                      {/* Video Embed */}
                      {process.youtubeShortUrl && (
                        <div className="aspect-video rounded-xl overflow-hidden bg-[var(--bg-tertiary)]">
                          <iframe
                            src={process.youtubeShortUrl.replace('watch?v=', 'embed/').replace('shorts/', 'embed/')}
                            title="Process Video"
                            className="w-full h-full"
                            allowFullScreen
                          ></iframe>
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              </article>
            ))}
          </div>
        )}

        {/* CTA Section */}
        <div className="mt-16 text-center animate-fade-in-up" style={{ animationDelay: '800ms' }}>
          <div className="card p-8 md:p-12">
            <h2 className="text-2xl font-bold mb-4 text-[var(--text-primary)]">
              Want to See Your Idea Come to Life?
            </h2>
            <p className="text-[var(--text-secondary)] mb-6 max-w-xl mx-auto">
              Every commission follows a similar process, with regular updates and collaboration opportunities at each stage.
            </p>
            <div className="flex flex-col sm:flex-row gap-4 justify-center">
              <Link
                href="/commissions"
                className="btn-primary inline-flex items-center justify-center gap-2"
              >
                Start a Commission
              </Link>
              <Link
                href="/portfolio"
                className="btn-secondary inline-flex items-center justify-center gap-2"
              >
                View Portfolio
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}